import "./App.css";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Router from "./router/Router";
import getUserData from "./firebase/getUserData";
import { LOGIN, LOGOUT } from "./store/authSlice";

function App() {
  const dispatch = useDispatch();
  const auth = useSelector(stats => stats.auth);

  useEffect(() => {
    const token = localStorage.getItem("seesame-t");

    if (token) {
      getUserData(token)
        .then((res) => {
          dispatch(LOGIN(res));
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, [])

  return (
    <div className="app-container">
      {auth.isLogin && <button type="button" style={{ position: "absolute", background: "white", width: "45px", height: "45px", borderRadius: "50%", border: "1px solid var(--color-background)", top: 0, right: 0 }} onClick={() => dispatch(LOGOUT())}>로그아웃</button>}
      <Router />
    </div>
  );
}
export default App;
